@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

/*
    colors
    Rich Black: #0D1321 
    Linen: #FFEDDF tannish
    June Bud: #C5D86D greenish
    Deep Taupe: #86615C
*/

*{
  margin: 0px !important;
  padding: 0px !important;
  box-sizing: border-box !important;
  font-family: Montserrat; /*maybe*/

  // background-color: #0D1321;
}

.home{
    background-color: #0D1321 !important;
    background-image: url(/images/lineyGuy.svg);
    background-repeat: no-repeat;
    background-size: 45%;
    background-position: center;
    color: #C5D86D !important;
}

.about{
    background-color: #C5D86D !important;
    background-image: url(/images/swirlyGuy.svg);
    background-repeat: no-repeat;
    background-size: 45%;
    background-position: center;
    color: #0D1321 !important;
}

.projects{
    background-color: #86615C !important;
    background-image: url(/images/swirlyGuy.svg);
    background-repeat: no-repeat;
    background-size: 45%;
    background-position: center;
    color: #0D1321 !important;
}

.contact{
    background-color: #FFEDDF !important;
    background-image: url(/images/swirlyGuy.svg);
    background-repeat: no-repeat;
    background-size: 45%;
    background-position: center;
    color: #0D1321 !important;
}

.home,
.about,
.projects,
.contact{
  height: 90vh;
  position: absolute;
  width: 100%;
}

.home-content,
.about-content,
.projects-content,
.contact-content{
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

img{
    border-radius: 50%;
    border: solid #86615C;
}

.nav-list{
  height: 10vh;
  display: flex;
  width: 45%;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  list-style: none;
  background-color: #0D1321;
  //margin: 0px !important;
  //padding: 0px !important;
  //box-sizing: border-box !important;
}

.nav-links{
  text-decoration: none;
  font-size: 18px;
}

a{
    color: #C5D86D !important;
}

p{
    color: #FFEDDF;
}

footer{
    position:absolute;
    bottom:0;
    height:50px;   /* Height of the footer */
 }